'use client';

import { useState, useRef, useEffect } from 'react';
import { Message } from './chatbotTypes';

const initialMessages: Message[] = [
  {
    id: 1,
    text: 'H√£y ch·ªâ ra cho t√¥i kho·∫£ng m·∫•y gi·ªù b√£o qua nh√† t√¥i',
    isUser: true,
  },
  {
    id: 2,
    text: 'D·ª±a tr√™n v·ªã tr√≠ nh√† c·ªßa b·∫°n v√† ƒë∆∞·ªùng ƒëi d·ª± b√°o c·ªßa c∆°n b√£o, d·ª± ki·∫øn m·∫Øt b√£o s·∫Ω ƒëi qua v√†o kho·∫£ng 3:15 s√°ng. T√¥i ƒë√£ ph√≥ng to b·∫£n ƒë·ªì ƒë·∫øn v·ªã tr√≠ c·ªßa b·∫°n ƒë·ªÉ b·∫°n c√≥ th·ªÉ theo d√µi tr·ª±c ti·∫øp.',
    isUser: false,
  },
];

export default function ChatbotTab() {
  const [messages, setMessages] = useState<Message[]>(initialMessages);
  const [inputText, setInputText] = useState('');
  const messagesEndRef = useRef<HTMLDivElement>(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const handleSend = () => {
    if (!inputText.trim()) return;

    const newMessage: Message = {
      id: messages.length + 1,
      text: inputText,
      isUser: true,
    };

    setMessages([...messages, newMessage]);
    setInputText('');

    // Simulate bot response
    setTimeout(() => {
      const botResponse: Message = {
        id: messages.length + 2,
        text: 'T√¥i ƒëang ph√¢n t√≠ch d·ªØ li·ªáu th·ªùi ti·∫øt ƒë·ªÉ tr·∫£ l·ªùi c√¢u h·ªèi c·ªßa b·∫°n...',
        isUser: false,
      };
      setMessages((prev) => [...prev, botResponse]);
    }, 1000);
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSend();
    }
  };

  return (
    <div className="flex flex-col h-full">
      {/* Messages */}
      <div className="flex-1 overflow-y-auto px-4 py-4 scrollbar-thin">
        <div className="flex flex-col gap-4">
          {messages.map((message) => (
            <div
              key={message.id}
              className={`flex ${message.isUser ? 'justify-end' : 'justify-start'}`}
            >
              <div
                className={`max-w-[80%] rounded-lg px-3 py-2 ${
                  message.isUser
                    ? 'bg-gray-700 text-white'
                    : 'bg-[#137fec] text-white'
                }`}
              >
                <p className="text-sm leading-normal">{message.text}</p>
              </div>
            </div>
          ))}
          <div ref={messagesEndRef} />
        </div>
      </div>

      {/* Input */}
      <div className="flex-shrink-0 px-4 pb-4 border-t border-white/10 pt-4">
        <div className="flex gap-2">
          <input
            type="text"
            value={inputText}
            onChange={(e) => setInputText(e.target.value)}
            onKeyDown={handleKeyPress}
            placeholder="Nh·∫≠p tin nh·∫Øn..."
            className="flex-1 rounded-lg border border-gray-700 bg-[#1c2127] px-3 py-2 text-sm text-white placeholder:text-gray-400 focus:ring-1 focus:ring-[#137fec] focus:border-[#137fec] outline-none"
          />
          <button
            onClick={handleSend}
            disabled={!inputText.trim()}
            className="px-4 py-2 rounded-lg bg-[#137fec] text-white hover:bg-[#137fec]/90 transition-colors disabled:opacity-50 disabled:cursor-not-allowed flex-shrink-0"
          >
            <svg
              className="w-5 h-5"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M12 19l9 2-9-18-9 18 9-2zm0 0v-8"
              />
            </svg>
          </button>
        </div>
      </div>
    </div>
  );
}
